<template>
  <el-dialog :title="id?'修改标签':'新增标签'"  :visible.sync="show" center width="550px" @close="resetForm('ruleForm')" :close-on-click-modal="false">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="ruleForm" label-width="130px">
      <el-form-item label="标签编码：" prop="labelId" v-if="id">
       <p>{{ruleForm.labelId||EMPTYTEXT}}</p>
      </el-form-item>
      <el-form-item label="标签简称：" :prop="!id?'labelShortName':''">
        <template v-if="!id">
          <el-input v-model.trim="ruleForm.labelShortName" placeholder="请输入标签简称" class="input-box" clearable maxlength="50"></el-input>
        </template>
        <template v-else>
          <p>{{ruleForm.labelShortName||EMPTYTEXT}}</p>
        </template>
      </el-form-item>
      <el-form-item label="标签名称：" :prop="!id?'labelName':''">
        <template v-if="!id">
          <el-input v-model.trim="ruleForm.labelName" placeholder="请输入标签名称" class="input-box" clearable maxlength="50"></el-input>
        </template>
        <template v-else>
          <p>{{ruleForm.labelName||EMPTYTEXT}}</p>
        </template>
      </el-form-item>
      <el-form-item label="标签RGB颜色：" prop="labelRgbCode">  
        <el-color-picker v-model="ruleForm.labelRgbCode" size="small"></el-color-picker>
      </el-form-item>
      <el-form-item label="标签描述：" prop="labelDesc">  
        <el-input v-model.trim="ruleForm.labelDesc" placeholder="请输入标签描述" class="input-box" clearable maxlength="100"></el-input>
      </el-form-item>
      <el-form-item label="状态：" prop="labelStatus">
         <el-select placeholder="请选择状态" v-model="ruleForm.labelStatus" class="input-box">
          <el-option v-for="item in statusOption" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="resetForm('ruleForm')">取 消</el-button>
      <el-button type="primary" @click="submitForm('ruleForm')">确 认</el-button>
    </div>
 </el-dialog>
</template>

<script>
export default {
  name: 'addTag',
  props: {
    value: {
      type: Boolean
    },
    id:{
      type:[String,Number],
      value:''
    }
  },
  model: {
    prop: 'value',
    event: 'change'
  },
  watch: {
    value (n) {
      this.show = this.value
      if(this.show === true){
        if(this.id){
          this.getLabelDetail() //获取标签详情
        }else{
          this.initForm() //初始化表单
        }
      }
    }
  },
  data () {
    return {
       show:this.value,
       ruleForm:{
         labelId:'',//标签编码
         labelShortName:'', //标签简称
         labelName:'', //标签名称
         labelRgbCode:'#f00', //标签颜色
         labelDesc:'', //标签描述
         labelStatus:1 //标签状态
       },
       statusOption:[
          {
            value: 1,
            label: '已启用'
          },
          {
            value: 2,
            label: '已禁用'
          }
       ],//状态下拉
       rules: {
        labelShortName: [
          { required: true, message: '标签简称不能为空', trigger: 'blur' }
        ],
        labelName: [
          { required: true, message: '标签名称不能为空', trigger: 'blur' }
        ],
        labelRgbCode: [
          { required: true, message: '请选择标签RGB颜色', trigger: 'change' }
        ],
        labelDesc: [
          { required: true, message: '标签描述不能为空', trigger: 'blur' }
        ],
        labelStatus: [
          { required: true, message: '请选择标签状态', trigger: 'change' }
        ]
      }
    }
  },
  methods: {
    //获取标签详情
    getLabelDetail(){
      this.$http.get(process.env.VUE_APP_API+'/family/label/getByLabelId?labelId='+this.id).then(res => {
        if(res.code===200){
          let result = res.data
          for(let key in this.ruleForm){
            this.ruleForm[key] = result[key]
          }
        }else{
          this.$message.error(res.msg);
        }
      })
      .catch(error => {
        this.$message.error(error);
      });
    },
    //保存设置
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if(this.id){
            this.ruleForm.labelId = this.id
            this.editLabel(this.ruleForm)
          }else{
            delete this.ruleForm.labelId
            this.addLabel(this.ruleForm)
          }
        }
      });
    },
    //添加标签
    addLabel(params){
      this.$http.post(process.env.VUE_APP_API+'/family/label/insert',params).then(res => {
        if(res.code===200){
          this.$message.success('添加成功');
          this.show = false
          this.$emit('change', false)
          this.$emit('refresh') //刷新列表
        }else{
          this.$message.error(res.msg);
        }
      })
      .catch(error => {
        this.$message.error(error);
      });
    },
    //编辑标签
    editLabel(params){
      this.$http.post(process.env.VUE_APP_API+'/family/label/update',params).then(res => {
        if(res.code===200){
          this.$message.success('修改成功');
          this.show = false
          this.$emit('change', false)
          this.$emit('refresh') //刷新列表
        }else{
          this.$message.error(res.msg);
        }
      })
      .catch(error => {
        this.$message.error(error);
      });
    },
    //初始化表单
    initForm(){
      Object.keys(this.ruleForm).forEach(k => this.ruleForm[k] = '')
      this.ruleForm.labelStatus = 1
      this.ruleForm.labelRgbCode = '#f00'
    },
    //取消设置
    resetForm(formName) {
      this.show = false
      this.$emit('change', false)
      this.$nextTick(()=>{
        this.$refs[formName].clearValidate();
      })
    }
  }
}
</script>

<style lang='less' scoped>
  /* 修改状态弹窗 */
  .ruleForm{
    width:430px;
    margin:0px auto;
    .input-box{
      width:300px;
    }
  }
</style>