<!-- 标签管理 -->
<template>
  <div class="list-common-box">
    <!-- 标题 -->
    <div class="title-box">
      <Breadcrumb :isLevel='2'></Breadcrumb>
       <div class="rightbox">
        <div class="item" @click="handleAdd()">
           <cc-svg-icon icon-class="add"></cc-svg-icon>
           <span>新增</span>
        </div>
      </div>
    </div>
    <!-- 主要数据 -->
    <div class="card-box">
      <!-- 搜索 -->
      <div class="search-bar">
        <!-- 左 -->
        <div class="search-left">
          <div class="search-item">
            <span class="search-label">标签名称</span>
            <el-input v-model.trim="searchKey.labelName" placeholder="请输入标签名称" clearable></el-input>
          </div>
          <div class="search-item">
            <span class="search-label">标签编码</span>
            <el-input v-model.trim="searchKey.labelId" placeholder="请输入标签编码" clearable></el-input>
          </div>
          <div class="search-item">
            <span class="search-label">状态</span>
            <el-select placeholder="请选择状态" v-model="searchKey.labelStatus" clearable>
              <el-option v-for="item in statusOption" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </div>
        </div>
        <div class="search-right">
          <el-button type="primary" plain icon="el-icon-search" @click="querylist()">查询</el-button>
          <el-button type="primary" plain icon="el-icon-delete" @click="Refreshlist()">清空</el-button>
        </div>
      </div>
      <!-- 表格 -->
      <div class="table-box">
        <el-table :data="dataList" tooltip-effect="dark" stripe height="100%"> 
          <el-table-column label="序号" align="center" width="80">
            <template slot-scope="scope">
              {{scope.$index+(pageNum - 1) * pageSize + 1}}
            </template>
          </el-table-column>
          <el-table-column prop="labelName" label="标签名称" align="center">
            <template slot-scope="scope">
              {{scope.row.labelName?scope.row.labelName:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="labelId" label="标签编码" align="center" width="190">
            <template slot-scope="scope">
              {{scope.row.labelId?scope.row.labelId:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="labelShortName" label="标签简称" align="center">
            <template slot-scope="scope">
              {{scope.row.labelShortName?scope.row.labelShortName:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="labelRgbCode" label="标签颜色RGB代码" align="center" width="140">
            <template slot-scope="scope">
              {{scope.row.labelRgbCode?scope.row.labelRgbCode:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="labelDesc" label="标签描述" align="center" :show-overflow-tooltip="true">
            <template slot-scope="scope">
              {{scope.row.labelDesc?scope.row.labelDesc:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="labelStatus" label="状态" align="center" width="80">
            <template slot-scope="scope">
              {{scope.row.labelStatus===1?'已启用':scope.row.labelStatus===2?'已禁用':EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="createDateStr" label="创建时间"  align="center" width="170">
            <template slot-scope="scope">
              {{scope.row.createDateStr?scope.row.createDateStr:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column label="操作"  align="center" width="80">
            <template slot-scope="scope">
              <cc-svg-icon icon-class="edit" @click="handleAdd(scope.row.labelId)"></cc-svg-icon>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <!-- 分页 -->
      <div class="page-box">
         <Paginaction class='pagination' :goPage="pageNum" :totalNum="totalNum" @updateList="changePage" ref="paginationEvent"/>
      </div>
    </div>
    <!-- 新增.编辑标签弹窗 -->
    <addTag v-model="tagDialog" :id="labelId" @refresh="getDatalist"></addTag>
  </div>
</template>

<script>
  import addTag from './add' //添加编辑标签
  export default {
    name: 'tag-list',
    components: {
      addTag
    },
    data() {
      return {
        searchKey:{
          labelName:'',//标签名称
          labelId:'', //标签编码
          labelStatus:'',//标签状态
        },
        statusOption:[
          {
            value: 0,
            label: '全部'
          },
          {
            value: 1,
            label: '已启用'
          },
          {
            value: 2,
            label: '已禁用'
          }
        ],
        pageNum:1,//当前页
        pageSize:10,//每页条数
        totalNum: 0, //总条数
        dataList: [],//标签列表
        tagDialog:false,//标签弹窗
        labelId:'',//标签id
      }
    },
    created() {
       this.getDatalist()
    },
    methods: {
      //新增编辑标签
      handleAdd(id){
        this.tagDialog = true
        this.labelId = id?id:''
      },
      //查询
      querylist() {
        this.pageNum = 1
        this.getDatalist()
      },
      //重置
      Refreshlist() {
        this.pageNum = 1
        this.pageSize = 10
        Object.keys(this.searchKey).forEach(k => this.searchKey[k] = '')
        this.getDatalist()
      },
      //分页改变
      changePage(query){
        this.pageNum = query.pageNum
        this.pageSize = query.pageSize
        this.getDatalist()
      },
      //获取标签列表
      getDatalist() {
        this.$emit("viewLoad", true);
        let params = {}
        params.pageNum  = this.pageNum
        params.pageSize = this.pageSize
        params = Object.assign({},params,this.searchKey)
        this.$http.post(process.env.VUE_APP_API + '/family/label/getByPage', params).then(res => {
          if (res.code===200) {
            this.dataList = res.data.list || [] //标签列表
            this.totalNum = res.data.total //总条数
            this.pageNum = res.data.pageNum //当前页
            this.pageSize = res.data.pageSize //每页条数
          } else {
            this.$message.error(res.msg);
          }
          this.$emit("viewLoad", false);
        })
        .catch(error => {
          this.$emit("viewLoad", false);
          this.$message.error(error);
        });
      }
    }
  }
</script>
<style lang='less' scoped>
</style>